import React, { useContext, useState, Fragment } from "react";
import AppContext from "app/appContext";
import { Col, Button, Container, Dropdown } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { Breadcrumb } from "@gull";
import { timeSlotCompress } from "../classrooms/functions/timeSlotCompress";
import { deleteClassroom } from "app/reducers/actions/ClassroomActions";
import { nanoid } from "nanoid";
const ClassroomLists = () => {
  const { classrooms, dispatch, user, token } = useContext(AppContext);
  const [search, setSearch] = useState("");
  const history = useHistory();
  const handleEdit = (id) => {
    history.push(`/manage/edit/${id}`);
  };

  const handleDelete = async (id, name) => {
    const notifications = {
      id: nanoid(),
      created: Date.now(),
      user: user.email,
      isViewed: false,
      action: "delete",
      content: {
        name: name,
        location: "manage",
        description: "click to see more information",
      },
    };
    deleteClassroom(id, notifications, token)(dispatch);
  };

  return (
    <Fragment>
      <Container>
        <Col lg={12} md={12} sm={8} xs={12} className="mb-4">
          <Breadcrumb
            routeSegments={[
              { name: "Home", path: "/dashboard" },
              { name: "Manage List" },
            ]}
          />
          <div className="card  ">
            <div className="card-body">
              <div className="ul-widget__head border-0 mb-2">
                <Link to="/manage/add">
                  <Button>
                    {" "}
                    <i className="nav-icon i-Add "></i> Add Classroom
                  </Button>
                </Link>
                <div className="ul-widget__head-label"></div>
                <form alignRight>
                  <input
                    className="form-control "
                    type="search"
                    placeholder="Search Here...."
                    onChange={(e) => setSearch(e.target.value.toLowerCase())}
                  />
                </form>
              </div>
              <table className="table">
                <thead>
                  <tr className="">
                    <th className="nowrap-th border-0 text-primary" scope="col">
                      ROOM
                    </th>
                    <th className="nowrap-th border-0 text-primary" scope="col">
                      TIME SLOT
                    </th>
                    <th className="nowrap-th border-0 text-primary" scope="col">
                      ACTIONS
                    </th>
                  </tr>
                </thead>
                {classrooms.length !== 0 && (
                  <tbody>
                    {classrooms
                      .filter((data) =>
                        search === ""
                          ? data
                          : data.roomName.toLowerCase().includes(search)
                      )
                      .map((data) => (
                        <tr className="border-bottom" key={data.id}>
                          <td className="border-0 ">
                            <span>{data.roomName}</span>
                          </td>
                          <td className="border-0 ">
                            {data.timeSlots &&
                              timeSlotCompress(
                                data.timeSlots,
                                data.timeSlots.length
                              ).map((timeSlot, idx) => (
                                <span key={idx}>{timeSlot}</span>
                              ))}
                          </td>
                          <td className="border-0 ">
                            <Button
                              onClick={(e) => handleEdit(data.id)}
                              variant="info"
                              className="mr-2"
                              size="sm"
                            >
                              Edit
                            </Button>
                            <Button
                              variant="danger"
                              size="sm"
                              onClick={(e) =>
                                handleDelete(data.id, data.roomName)
                              }
                            >
                              Delete
                            </Button>
                          </td>
                        </tr>
                      ))}
                  </tbody>
                )}
              </table>
            </div>
            {classrooms.length === 0 && (
              <div className="mx-auto border border-light rounded p-3 pb-0 mb-5">
                <p className="text-center text-muted">No Data Found</p>
              </div>
            )}
          </div>
        </Col>
      </Container>
    </Fragment>
  );
};

export default ClassroomLists;
